.menu {

  &__title {
    color: #393939;
    font-family: "Open Sans", sans-serif;
    font-size: 34px;
    font-weight: 400;
    margin-bottom: 37px;
  }

  &__list {
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 532px;
    height: 62px;
    border: 1px solid #f2f2f2;
    padding: 0 12px 5px 12px;
    box-sizing: border-box;
  }

  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    flex-grow: 1;
    transition: 0.2s color ease-in-out;
    color: #000000;
    font-family: "Source Sans Pro";
    font-size: 16px;
    cursor: pointer;

    &_active {
      color: #283593;
    }

    &:hover {
      color: #ef6c00;
    }
  }
}